import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import './App.css';
import './App.scss';
import Navigation from './Navigation';
import Home from './components/Home'
import About from './components/About'
import Austin from './components/Austin'
import LA from './components/Los_Angeles'
import Miami from './components/Miami'
import SanFran from './components/San_Francisco'
import EU from './components/EU'
import Footer from './Footer'

function App() {
    return (
        <Router>
            <div id="top">
                <Navigation />
            </div>

            <div id='middle'>
                <Switch>
                    <Route exact path='/Test' component={Home} />
                    <Route path='/about' component={About} />
                    <Route path='/eu' component={EU} />
                    <Route path='/Austin' component={Austin} />
                    <Route path='/LA' component={LA} />
                    <Route path='/Miami' component={Miami} />
                    <Route path='/SanFran' component={SanFran} />
                </Switch>
            </div>
            
            <div id='bottom'>
                <Footer />
            </div>
        </Router>
    )};

export default App;


