import React, { useState, useEffect } from 'react';
import { Dropbox } from 'dropbox';

function Test(req) {
    const [test, setTest] = useState([]);
    const hect = [];

    useEffect(() => {
        req.filesListFolder({
          path: '/america/austin'
        }).then(res => {
          res.entries.forEach(element => {
          hect.push({path: "/america/austin/" + element.name, size: 'w1024h768'})
          })

          req.filesGetThumbnailBatch({
            "entries": hect
        }).then(res => {
            setTest(res.entries)
        }) 
        }
        )
    }, [])
  return test
}

function EU() {
    const accessToken = '6traYkJcKf4AAAAAAAAX3rCwSMZIas7r8YideGBiAwQVa8x744QlHNXiKs4Rnw7d';

    const dbx = new Dropbox({  
        accessToken,  
        fetch  
    });

    const testing = Test(dbx)
    const photos = [];
    let counter = 1;
    const zoom = []

    if (testing.length > 0) {
      testing.forEach(element => {
        photos.push(<a href={'#lightbox-' + counter}><img src={"data:image/jpeg;base64," + element.thumbnail} alt="" /></a>)
        zoom.push(<div className="lightbox" id={'lightbox-' + counter}><div className="content"><img src={"data:image/jpeg;base64," + element.thumbnail} /><a className="close" href="#gallery"></a></div></div>)
        counter++  
      })
    }
      
    return (
        <div className="Gallery">
        <div>
            <h1>Welcome to the Austin Gallery</h1>
            <p>What you see here is my home and my people. I come from a German people in a small town know by it's secrecy as HuddleDorf. The town has present the ease of imagery through it's mesmorizing scenes and jaw dropping country sunset colouring. Please enjoy.</p>
        </div>
        <div className="GalleryBox">
          {photos}
          {zoom}
        </div>
        </div>
  )};

export default EU;